import React from "react";
import {Link, useParams} from "react-router-dom";
import {Container} from "react-bootstrap";
import logoImg from "../assets/icons/logo.svg";
import BackButton from "../components/BackButton";
import {useNavigate} from 'react-router-dom';
import axios from "axios";

const CheckEmail = () => {
    const params = useParams();
    const navigation = useNavigate();


    const resendVerificationCode = () => {
        if (params.email !== null) {
            axios.get(
                'http://localhost:8081/api/v1/auth/forgot-password?email=' + params.email,
            ).then(res =>
                alert("Success sent")
            ).catch(err => {
                console.log(err)
            })
        }
    }

    const backLogin = () => {
        navigation("/sign-in")
    }
    return (
        <Container className="container center" style={{marginTop: "40px"}}>

            <img src={logoImg} className={"logo-icon"} alt={"logo-img"}/>
            <h2 className="verify-title">Check your email</h2>
            <h5 className="check-mail" style={{marginTop: "9px"}}>We have sent password reset instructions
                to: {params.email}</h5>
            <h5 className="check-mail"
                style={{marginTop: "10px", marginBottom: "49px"}}>If it doesn't arrive soon, check your spam folder or
                <Link className="click-link" onClick={resendVerificationCode}> send the email again</Link></h5>

            <BackButton onClick={backLogin}>Back to log in</BackButton>
        </Container>

    )
}

export default CheckEmail;