import React, {useState, useCallback} from 'react'
import {useNavigate} from 'react-router-dom';
import {Container} from 'react-bootstrap';
import '../index.css';
import axios from "axios";
import Input from "../components/Input"
import Button from "../components/Button"
import BackButton from "../components/BackButton";

const ForgotPassword = () => {
    const [email, setEmail] = useState();
    const [notRegistered, setNotRegistered] = useState(false);
    const [errorMessage, setErrorMessage] = useState('');
    const navigation = useNavigate();

    const resetPassword = (e) => {
        if (email !== null) {
            axios.get(
                'http://localhost:8081/api/v1/auth/forgot-password?email=' + email,
            ).then(res => {
                    if (res.data.data.errorEmail) {
                        setErrorMessage(res.data.data.message);
                        setNotRegistered(res.data.data.errorEmail);
                    } else {
                        navigation("/check-email/" + email)
                    }
                }
            ).catch(err => {
                console.log(err)
            })
        }
    }

    const backLogin = () => {
        navigation("/sign-in")
    }

    const onChange = useCallback((v) => {
        if (notRegistered) {
            setErrorMessage('')
            setNotRegistered(false)
        }
        setEmail(v);
    },[notRegistered])

    return (
        <Container className="container center" style={{marginTop: "40px"}}>

            <h4 className="h4">Forgot password?</h4>

            <p className="instructions">
                Enter the email address and we'll send you reset instructions.
            </p>
            <div className={''} style={{width: "270px"}}>
                <Input
                    type={"email"}
                    labelText={"E-mail"}
                    required
                    placeholder={"Email"}
                    mt={4}
                    mb={4}
                    onChange={onChange}
                    isError={notRegistered}
                    errorMessage={errorMessage}
                />

                <Button
                    mt={"32"}
                    mb={"16"}
                    onClick={resetPassword}
                >Reset password</Button>

                <BackButton
                    onClick={backLogin}
                >Back</BackButton>
            </div>
        </Container>
    );
}

export default ForgotPassword;
