import React from "react"
import styled, {css} from "styled-components";

const Style = styled.button`
  border: 2px solid #008ACE;
  background: #FFFFFF;
  border-radius: 3px;
  height: 42px;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  color: #008ACE;
  outline: none;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.3s;

  &:active {
    transform: scale(0.9);
  }

  ${({mb}) => mb && css`
    margin-bottom: ${mb}px;
  `}
  ${({mt}) => mt && css`
    margin-top: ${mt}px;
  `}
`

const Button = ({
                    type = "button",
                    children,
                    disabled,
                    onClick = () => '',
                    mb,
                    mt
                }) => {
    return (
        <Style type={type} onClick={() => !disabled && onClick()} {...{mb, mt}} >{children}</Style>
    )
}
export default Button