import React, {useState} from 'react'
import {Container} from 'react-bootstrap';
import '../index.css';
import axios from "axios";
import Input from "../components/Input"
import Button from "../components/Button"
import BackButton from "../components/BackButton";
import {useParams, useNavigate} from "react-router-dom";


const ChangePassword = () => {
    const [password, setPassword] = useState();
    const [prePassword, setPrePassword] = useState();
    const [passwordNotEquals, setPasswordNotEquals] = useState(false);
    const [passwordLess8Chars, setPasswordLess8Chars] = useState(false);
    const params = useParams();
    const navigation = useNavigate();

    const backLogin = () => {
        navigation("/sign-in")
    }


    const changePassword = () => {
        if (password < 8) {
            setPasswordLess8Chars(true)
        } else {
            if (password === prePassword) {
                setPasswordNotEquals(false)
                axios.post(
                    'http://localhost:8081/api/v1/auth/reset-password',
                    {
                        password,
                        prePassword,
                        email: params.email,
                        verificationCode: params.verificationCode
                    }
                ).then(res => {
                        navigation("/sign-in")
                    }
                ).catch(err => {
                    console.log(err)
                    alert("error")
                })
            } else {
                setPasswordNotEquals(true)
            }
        }
    }

    return (
        <Container className="container center" style={{marginTop: "40px"}}>

            <h4 className="h4">Change password</h4>

            <div className={''} style={{width: "270px"}}>
                <Input
                    type={"password"}
                    labelText={"Enter new password"}
                    required
                    placeholder={"Password"}
                    mt={4}
                    mb={4}
                    onChange={(v) => setPassword(v)}
                    errorMessage={passwordLess8Chars ? "Your password must be at least 8 characters" : ""}
                    isError={passwordLess8Chars}
                />
                <label className="label">Your password must be at least 8 characters </label>
                <Input
                    type={"password"}
                    labelText={"Repeat new password"}
                    required
                    placeholder={"Password"}
                    mt={4}
                    mb={4}
                    onChange={(v) => setPrePassword(v)}
                    errorMessage={passwordNotEquals ? "Passwords don't match" : ""}
                    isError={passwordNotEquals}
                />
                <label className="label">Your password must be the same </label>
                <Button
                    mt={"38"}
                    mb={"16"}
                    onClick={changePassword}>
                    Change password</Button>

                <BackButton
                    onClick={backLogin}>Back</BackButton>
            </div>
        </Container>
    );
}

export default ChangePassword;
