import React, {useCallback, useState} from "react";
import styled, {css} from "styled-components"
import eyeImg from "../assets/icons/eye.svg"

const Style = styled.div`
  --error: #E54322;

  label {
    width: 100%;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    color: #474A59;

    .required {
      color: red;
    }
  }

  .form-input {
    ${({labelText}) => labelText && css`
      margin-top: 6px;
    `}
    height: 42px;
    background: #FFFFFF;
    border: 1px solid ${({isFocused, isError}) => isFocused ? "#008ACE" : isError ? "var(--error)" : "#CED0DB"};
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 9px 6px 9px 12px;
    transition: 0.2s;
    position: relative;

    .eye-icon {
      cursor: pointer;
    }

    ${({star, type, isOpen}) => type === "password" && !isOpen && css`
      &:after {
        content: "${star}";
        position: absolute;
        top: 12.5px;
        left: 13.5px;
        font-weight: 400;
        font-size: 14px;
        line-height: 24px;
        color: #6C6F80;

      }
    `}
    input {
      border: none;
      outline: none;
      background: none;
      font-weight: 400;
      font-size: 16px;
      line-height: 24px;
      color: #6C6F80;

      &[type="password"] {
        letter-spacing: 0.99px;
        color: #FFFFFF;
        caret-color: #000;
      }

    }

  }

  .error-message {
    font-weight: 400;
    font-size: 12px;
    line-height: 18px;
    color: ${({isError}) => isError ? "var(--error)" : "#6C6F80"};
    margin-top: 4px;

  }

  ${({mb}) => mb && css`
    margin-bottom: ${mb}px;
  `}
  ${({mt}) => mt && css`
    margin-top: ${mt}px;
  `}

`

function replaceStar(value) {
    let len = String(value).length;
    let temp = "";
    for (let i = 0; len > i; i++) {
        temp += "*";
    }
    return temp;
}


const Input = ({
                   type = "text",
                   onChange = () => "",
                   labelText = '',
                   required,
                   placeholder = "",
                   mt = "",
                   mb = "",
                   errorMessage = "",
                   isError = false
               }) => {
    const [state, setState] = useState({
        isFocused: false,
        isOpen: false,
    });
    const [inputValue, setInputValue] = useState("")


    const clickEye = useCallback(() => setState(s => ({...s, isOpen: !s.isOpen})), [])

    const changeInputHandler = useCallback((event) => {
        setInputValue(event.target.value);
        onChange(event.target.value);
    }, [onChange])

    const onFocusHandler = useCallback(() => setState(s => ({...s, isFocused: true})), [])
    const onBlurHandler = useCallback(() => setState(s => ({...s, isFocused: false})), [])

    return (
        <>
            <Style {...{
                isOpen: state.isOpen,
                isFocused: state.isFocused,
                star: replaceStar(inputValue),
                type,
                labelText,
                mt,
                mb,
                isError
            }}>
                <label>
                    {labelText} {required && <span className={"required"}>*</span>}
                    <div className={"form-input"}>
                        <input type={state.isOpen ? "text" : type} onChange={changeInputHandler}
                               onFocus={onFocusHandler}
                               onBlur={onBlurHandler} {...{placeholder}}/>
                        {type === "password" &&
                            <img src={eyeImg} className={"eye-icon"} alt={"eye-img"} onClick={clickEye}/>}
                    </div>
                </label>
                {errorMessage && <p className={"error-message"}>{errorMessage}</p>}
            </Style>

        </>
    )
}

export default Input