import React, {useState} from 'react'
import {Container} from 'react-bootstrap';
import '../index.css';
import axios from "axios";
import Input from "../components/Input"
import Button from "../components/Button"
import {Link} from "react-router-dom";

const SignUp = () => {
    const [email, setEmail] = useState();
    const [password, setPassword] = useState();
    const [prePassword, setPrePassword] = useState();
    const [passwordNotEquals, setPasswordNotEquals] = useState(false);

    const signUp = (e) => {
        // if (password === prePassword) {
        setPasswordNotEquals(false)
        axios.post(
            'http://localhost:8081/api/v1/auth/sign-up',
            {email, password, prePassword}
        ).then(res => {
                if (!res.data.data.errorEmail && !res.data.data.errorPassword) {
                    console.log(res)
                    window.location.replace("/verify-email/" + email)
                }else{
                    // if (res.data.data.errorEmail){
                    //
                    // }
                }
            }
        ).catch(err => {
            console.log(err)
        })
        // } else {
        //     setPasswordNotEquals(true)
        // }
    }

    return (
        <Container className="container center" style={{marginTop: "40px"}}>

            <h4 className="h4">Sign up</h4>

            <div className={''} style={{width: "270px"}}>
                <Input
                    type={"email"}
                    labelText={"E-mail"}
                    required
                    onChange={(v) => setEmail(v)}
                    placeholder={"Email"}
                    mt={9}
                    mb={16}
                />
                <Input
                    type={"password"}
                    labelText={"Password"}
                    required
                    placeholder={"Password"}
                    mt={4}
                    mb={4}
                    onChange={(v) => setPassword(v)}
                    errorMessage={password < 8 ? "Your password must be at least 8 characters" : ""}
                    isError={passwordNotEquals}
                />
                <label className="label">Your password must be at least 8 characters </label>
                <Input
                    type={"password"}
                    labelText={"Repeat password"}
                    required
                    placeholder={"Password"}
                    mt={6}
                    mb={32}
                    onChange={(v) => setPrePassword(v)}
                    errorMessage={passwordNotEquals ? "Passwords don't match" : ""}
                    isError={passwordNotEquals}
                />

                <Button
                    mt={"32"}
                    mb={"16"}
                    onClick={signUp}
                >Sign Up</Button>
                <p className="p-2">Already a user?{' '}
                    <Link to="/sign-in">Log in.</Link>
                </p>
            </div>
        </Container>
    );
}

export default SignUp;
