import React from "react";
import {Link, useParams} from "react-router-dom";
import {Container} from "react-bootstrap";
import logoImg from "../assets/icons/logo.svg";
import axios from "axios";
// import {ToastContainer, toast} from "react-toastify";


const VerifyEmail = () => {
    const params = useParams();

    const resendVerifyCode = () => {
        // toast.success("Hello world")
        axios.get("http://localhost:8081/api/v1/auth/resend-verification-code?email=" + params.email)
            .then(res => alert(res.data.data))
            .catch(err => console.log(err));
    }
    return (
        <Container className="container center" style={{marginTop: "40px"}}>

            <img src={logoImg} className={"logo-icon"} alt={"logo-img"}/>
            <h2 className="verify-title">Verify your email</h2>
            <h5 className="verify-text">We have sent verification link to your e-mail {params.email}. Please verify your
                e-mail address to continue using CloudGantt.</h5>

            <div className="link-wrapper">
                <Link className="click-link" onClick={resendVerifyCode}>Click here</Link>
                <p className="p-3">if you didn't receive an e-mail </p>
            </div>
        </Container>

    )
}

export default VerifyEmail;