import React, {useEffect, useState} from 'react'
import {Container} from 'react-bootstrap';
import '../index.css';
import axios from "axios";
import Input from "../components/Input"
import Button from "../components/Button"
import {Link, useNavigate} from "react-router-dom";


const SignIn = () => {
    const [email, setEmail] = useState();
    const [password, setPassword] = useState();
    const navigation = useNavigate();


    useEffect(() => {
        if (localStorage.getItem("AccessToken")) {
            axios.get("http://localhost:8081/api/v1/user/me",
                {
                    headers: {"Authorization": localStorage.getItem("AccessToken")}
                })
                .then(res => {
                    navigation("/cabinet")
                })
                .catch(err => console.log(err));
        }
    }, [])
    const signIn = () => {
        axios.post(
            'http://localhost:8081/api/v1/auth/sign-in',
            {email, password})
            .then(res => {
                localStorage.setItem("AccessToken", res.data.data.tokenType + res.data.data.accessToken);
                localStorage.setItem("RefreshToken", res.data.data.refreshToken);
                window.location.replace("/cabinet")
            })
            .catch(err => console.log(err))
    }
    return (
        <Container className="container center" style={{marginTop: "62px"}}>

            <h4 className="h4">Log in</h4>

            <div className={''} style={{width: "270px"}}>
                <Input
                    type={"email"}
                    labelText={"E-mail"}
                    required
                    onChange={(v) => setEmail(v)}
                    placeholder={"Email"}
                    mt={9}
                    mb={16}
                />
                <Input
                    type={"password"}
                    labelText={"Password"}
                    required
                    placeholder={"Password"}
                    mt={4}
                    mb={4}
                    onChange={(v) => setPassword(v)}
                />
                <Link className="label-1" to="/forgot-password">Forgot password? </Link>

                <Button
                    mt={"32"}
                    mb={"16"}
                    onClick={signIn}
                >Log in</Button>
                <p className="p-2">Need an account?{' '}
                    <Link to="/sign-up">Sign up.</Link>
                </p>
            </div>
        </Container>
    );
}

export default SignIn;
